<template>
	<div class="basicSetuUp">
		<div class="basicSetuUp_title">基本设置</div>
		<!-- 按钮 -->
		<div class="basicSetuUp_item">
			<div class="basicSetuUp_item_lift">
				<span>自动回复</span>
				<i-switch v-model="switch1" @on-change="change"></i-switch>
			</div>

			<div class="basicSetuUp_item_right">
				<span>自动追粉</span>
				<i-switch v-model="switch2" @on-change="change1"></i-switch>
			</div>
		</div>
		<div class="basicSetuUp_item">
			<div class="basicSetuUp_item_title">原始id</div>
			<div class="basicSetuUp_item_input">
				<Input disabled v-model="value" placeholder="原生id" style="width: 300px" />
			</div>
			<div class="basicSetuUp_item_button cursor-pointer mgright_15"  @click="handCopy">
				复制
			</div>
			<div class="basicSetuUp_item_button cursor-pointer"  @click="getReAuthLink">
				重新获取
				<!-- <Button  @click="getReAuthLink" style="background: #2d8cf0;">重新获取</Button> -->
			</div>
		</div>
		<div class="basicSetuUp_item">
			<div class="basicSetuUp_item_title">公众号名称</div>
			<div>{{essenceInfo.name}}</div>
		</div>
		<div class="basicSetuUp_item">
			<div class="basicSetuUp_item_title">APPID</div>
			<div>{{essenceInfo.appId}}</div>
		</div>
		<div class="basicSetuUp_item">
			<div class="basicSetuUp_item_title">公司主体</div>
			<div>{{essenceInfo.companyName}}</div>
		</div>
		<div class="basicSetuUp_item">
			<div class="basicSetuUp_item_title">行业</div>
			<div>
				<Select v-model="model1" style="width:200px" @on-change="handClick">
					<Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
				</Select>
			</div>
		</div>
		<div class="basicSetuUp_item">
			<div class="basicSetuUp_item_title">二维码</div>
			<div>
				<img :src="essenceInfo.qr" width="160px" height="160px" />
			</div>
		</div>
	</div>
</template>

<script>
	import {
		indexApiModules
	} from '@/api';
	export default {
		data() {
			return {
				wxAppId: localStorage.getItem('wxAppId'),
				list: ['微信公众号', '微信小程序', '抖音企业号'],
				essenceInfo: {},
				switch1: false,
				switch2: false,
				value: '',
				model1: '',
				cityList: [{
						value: '1',
						label: '婚纱'
					},
					{
						value: '2',
						label: '儿童'
					},
					{
						value: '3',
						label: '古装'
					},
					{
						value: '4',
						label: '写真'
					},
				],
			}
		},
		mounted() {
			this.getCompanyGetWXAPP()
		},
		methods: {
			getCompanyGetWXAPP() {
				indexApiModules.companyGetWXAPP({
					wxAppId: this.wxAppId
				}).then(res => {
					console.log('', res)
					this.essenceInfo = res.data
					this.switch1 = Boolean(this.essenceInfo.isAutoReply)
					this.switch2 = Boolean(this.essenceInfo.isAutoPursuit)
					this.value = this.essenceInfo.wxSourceId
					this.model1 = String(this.essenceInfo.industry)

				})
			},
			change(status) {
				let isAutoReply = Number(status)
				indexApiModules.companySetIsAutoReply({
					wxAppId: this.wxAppId,
					isAutoReply: isAutoReply,
				}).then(res => {
					console.log(res)
					this.getCompanyGetWXAPP()
					this.$Message.success('修改成功');
				})
			},
			change1(status) {
				let isAutoPursuit = Number(status)
				indexApiModules.companySetIsAutoPursuit({
					wxAppId: this.wxAppId,
					isAutoPursuit: isAutoPursuit,
				}).then(res => {
					console.log(res)
					this.getCompanyGetWXAPP()
					this.$Message.success('修改成功');
				})
			},
			// 设置行业
			handClick(value) {
				this.$Modal.confirm({
					title: '提示',
					content: '<p>您确定修改行业类型吗,修改行业类型会导致默认对话包发生改变</p>',
					loading: true,
					onOk: () => {
						indexApiModules.companySetindustry({
							wxAppId: this.wxAppId,
							industry: value,
						}).then(res => {
							console.log(res)
							this.$Modal.remove();
							this.getCompanyGetWXAPP()
							this.$Message.success('修改成功');
						})
					}
				});
			},
			// 重新授权
			getReAuthLink() {
				let api = process.env.VUE_APP_BASE_API
				let url = 'other/getReAuthLink'
				let companyId = this.$cookies.get('companyId')
				let apiurl = api + url + '?companyId=' +companyId
				console.log(apiurl);
				window.open(apiurl,"_blank");
				
				// indexApiModules.companyGetReAuthLink({
				// 	wxAppId: this.wxAppId,
				// }).then(res => {
				// 	let url = res.data.url
				// 	window.open(url, '_blank');
				// })
			},
			handCopy(){
				   var input = document.createElement("input");   // js创建一个input输入框
				   input.value = this.value;  // 将需要复制的文本赋值到创建的input输入框中
				   document.body.appendChild(input);    // 将输入框暂时创建到实例里面
				   input.select();   // 选中输入框中的内容
				   document.execCommand("Copy");   // 执行复制操作
				   document.body.removeChild(input); // 最后删除实例中临时创建的input输入框，完成复制操作
				   this.$Message.success('复制成功');
			},
			handGoBasic() {},
		},
		created: function() {

		}
	};
</script>

<style lang="less" scoped>
	.basicSetuUp {
		font-size: 16px;
		color: #333333;

		.basicSetuUp_title {
			padding: 14px 16px;
			border-bottom: 1px solid #DDDDDD;
		}

		.basicSetuUp_item {
			display: flex;
			margin-top: 25px;
			margin-left: 16px;

			.basicSetuUp_item_lift {
				margin-right: 150px;

				span {
					margin-right: 10px;
				}
			}

			.basicSetuUp_item_right {
				span {
					margin-right: 10px;
				}
			}

			.basicSetuUp_item_title {
				width: 130px;
			}

			.basicSetuUp_item_input {
				margin-right: 20px;
			}
			.basicSetuUp_item_button{
				padding: 4px 15px;
				// height: 32px;
				background: #2d8cf0;
				border-radius: 4px;
				color: #fff;
				font-size: 16px;
			}

		}
	}
</style>
